import React from 'react';
import PropTypes from 'prop-types';
import { iconList } from '../../../../utilities/icons'
import './skl-ds-divider.css';

//const iconList = icomoonSelection.icons.map((icon) => icon.properties.name);


export const Divider = ({ color, size}) => {
  return (
    <div class="skl-ds-divider"></div>
  );
};

Divider.propTypes = {
  iconName: PropTypes.oneOf(iconList).isRequired, // Nome dell'icona (senza "icon-")
  color: PropTypes.string, // Colore dell'icona
  size: PropTypes.oneOf(['small','medium', 'large', 'xlarge'])
};

Divider.defaultProps = {
  iconName: 'alarm',
  color: '#000', 
};
